import axios from 'axios';

const url = 'api/auth';

class AuthService {

    static getUsers() {
        return new Promise(async (resolve, reject) => {
            try {
                const res= await axios.get(url);
                const data = res.data;
                resolve(data);
            }catch(err) {
                reject(err);
            }
        })
    }

    static insertUser(username, email, password ) {
        return axios.post(url, {
            username,
            email,
            password
        });
    }
}

export default AuthService;