<template>
    <div>
        <h2>Login</h2>
        <div class="container">
            <h1>User text</h1>
            <p class="text">{{ users }} </p>
        </div>
        <form>
            <div class="form-group">
                <label for="username">Username</label>
                <input type="text" v-model="username" name="username" class="form-control" :class="{ 'is-invalid': submitted && !username }" />
                <div v-show="submitted && !username" class="invalid-feedback">Username is required</div>
            </div>
            <div class="form-group">
                <label htmlFor="email">Email</label>
                <input type="text" v-model="email" name="email" class="form-control" :class="{ 'is-invalid': submitted && !username }" />
                <div v-show="submitted && !email" class="invalid-feedback">Email is required</div>
            </div>
            <div class="form-group">
                <label htmlFor="password">Password</label>
                <input type="password" v-model="password" name="password" class="form-control" :class="{ 'is-invalid': submitted && !password }" />
                <div v-show="submitted && !password" class="invalid-feedback">Password is required</div>
            </div>
            <div class="form-group">
                <button v-on:click="createUser" class="btn btn-primary">Register</button>
            </div>
        </form>
    </div>
</template>

<script>

import AuthService from '../AuthService';

export default {
    data () {
        return {
            username: '',
            email:'',
            password: '',
            submitted: false,
            users: String
        }
    },

    async created() {
        try {
            this.users = await AuthService.getUsers();

        } catch(err) {
            this.err = err.message;
        }
    },
    computed: {
       
    },

    methods: {
        async createUser() {
            await AuthService.insertUser(this.username, this.email, this.password);
        }
    }
};
</script>

